// time

.time {
  @media (min-width: $xl) {
    margin-bottom: 0;
  }
}

.time__content {
  margin: 0;
  padding: 0;
  list-style: none;

  @media (min-width: $m) {
    max-width: 728px;
  }
}

.time__item {
  margin-bottom: 18px;

  &:first-child {
    margin-bottom: 4px;

    .time__label {
      display: flex;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.time__item--textarea {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.time__distance {
  color: $basic-light;
}

.time__label {
  position: relative;
  display: none;
  flex-wrap: wrap;
  justify-content: space-between;
}

.time__state {
  position: relative;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 12px;
    width: 16px;
    height: 16px;
    border: 4px solid $basic-white;
    border-radius: 100%;
    transition-property: border-color;
    transition-duration: 0.2s;
    transition-timing-function: linear;
    transform: translateY(-50%);
  }

  &::after {
    content: "Не Выбрано";
    left: auto;
    right: 10px;
    display: block;
    width: 119px;
    min-height: 24px;
    padding-top: 4px;
    padding-right: 14px;
    padding-bottom: 4px;
    padding-left: 13px;
    font-size: 13px;
    line-height: 1.19;
    font-weight: 400;
    text-transform: uppercase;
    border: none;
    border-radius: 2px;
    background-color: $page-light-gray;
    box-shadow: -25px 0 14px 2px $page-gray;
    opacity: 0.4;
    transition-property: opacity;
    transition-duration: 0.2s;
    transition-timing-function: linear;
  }
}

.time__state--valid {
  &::before {
    border-color: $page-red;
  }

  &::after {
    opacity: 1;
  }
}

.time__input {
  padding-left: 35px;
  padding-right: 130px;
  color: $basic-white;
  background-color: $page-gray;

  &::placeholder {
    color: $basic-white;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.time__textarea {
  width: 100%;
  min-height: 80px;
  padding: 13px;
  resize: none;
  font-size: 15px;
  line-height: 1.192;
  font-weight: 400;
}
