// direct-form
.direct-form {
  color: $basic-white;
  background-color: $page-black;
}

.direct-form__content {
  padding-top: 41px;
  padding-bottom: 37px;

  @media (min-width: $xl) {
    position: relative;
    width: 1140px;
    margin-right: auto;
    margin-left: auto;
    padding-top: 56px;
    padding-bottom: 129px;
  }
}

.direct-form__title {
  margin-bottom: 22px;
  font-size: 32px;
  line-height: 1.25;
  font-weight: 700;
}

.direct-form__field {
  margin: 0;
  margin-bottom: 42px;
  padding: 0;
  border: none;
}

.direct-form__legend {
  position: relative;
  margin-bottom: 16px;
  padding-left: 32px;
}

.direct-form__count {
  position: absolute;
  top: 50%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  font-size: 16px;
  line-height: 1.22;
  font-weight: 700;
  border-radius: 100%;
  background-color: $page-red;
  transform: translateY(-50%);
}

.direct-form__label {
  margin-bottom: 8px;
}

.direct-form__input {
  display: block;
  width: 100%;
  height: 44px;
  padding-top: 13px;
  padding-right: 18px;
  padding-bottom: 13px;
  padding-left: 14px;
  margin: 0;
  border: none;
}

.direct-form__disclaimer {
  padding: 24px;
  font-size: 15px;
  line-height: 1.192;
  font-weight: 400;
  background-color: $page-gray;

  @media (min-width: $m) {
    max-width: 728px;
  }

  @media (min-width: $xl) {
    position: absolute;
    top: 120px;
    right: 20px;
    width: 356px;
  }
}
