// _content

._content {
  padding-right: $padding-xs;
  padding-left: $padding-xs;

  @media (min-width: $s) {
    padding-right: $padding-s;
    padding-left: $padding-s;
  }

  @media (min-width: $m) {
    padding-right: $padding-m;
    padding-left: $padding-m;
  }

  @media (min-width: $l) {
    padding-right: $padding-l;
    padding-left: $padding-l;
  }
}
