// lift-capacity-field

.lift-capacity__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;

  @media (min-width: $m) {
    max-width: 356px;
  }
}

.lift-capacity__item {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 48%;
}
