// cargo
.cargo__content {
  margin: 0;
  padding: 0;
  list-style: none;

  @media (min-width: $m) {
    max-width: 356px;
  }
}

.cargo__type {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;

  .cargo__wrap {
    width: 48.3%;
  }

  @media (min-width: $m) {
    .cargo__wrap--type {
      width: 100%;
    }
  }
}

.cargo__wrap {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-bottom: 16px;
}

.cargo__wrap--danger {
  display: none;

  @media (min-width: $m) {
    display: flex;
  }
}

.cargo__temp {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  .cargo__wrap:first-child {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    margin-bottom: 0;
  }

  .cargo__wrap {
    display: none;
    width: 48.3%;
    margin-bottom: 16px;
  }
}

// *temp states

.temp-active {
  .cargo__wrap:first-child {
    margin-bottom: 16px;
  }

  .cargo__wrap {
    display: flex;
    margin-bottom: 0;
  }
}

// *custom checkbox

.cargo__check-label {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 24px;
  padding-left: 36px;
  user-select: none;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 24px;
    height: 24px;
    border: 2px solid $basic-white;
    transform: translateY(-50%);
  }

  &::after {
    content: "";
    position: absolute;
    display: none;
    top: 7px;
    left: 6px;
    width: 11px;
    height: 8px;
    border: 3px solid $basic-white;
    border-top-style: none;
    border-right-style: none;
    transform: rotate(-45deg);
  }
}

// * custom checkbox states

.cargo__check[checked] + .cargo__check-label {
  &::before {
    background-color: $page-red;
    border: none;
  }

  &::after {
    display: block;
  }
}

.cargo__check:focus + .cargo__check-label {
  &::before {
    box-shadow: 0 0 0 2px $basic-black, 0 0 0 3px $basic-white;
  }
}
